import _ from 'lodash';
import React, { Component } from 'react';
import ReactHighcharts from 'react-highcharts';

import constants from '../../constants';

export default class DistributionChart extends Component {
  getDistributionData = () => {
    const {
      counterfactualEnabled,
      counts,
      distributions,
      position,
      sampleRate,
      showTotals,
      variations,
    } = this.props;

    const series = [];
    
    Array(variations).fill(1).forEach((value, index) => {
      series.push({
        color: constants.COLORS[index],
        name: !!index ? `Variation ${index}` : 'Original',
        data: distributions[index].slice(0, Math.ceil(position / sampleRate) + 1),
        animation: {
          duration: 0,
        },
      })
    });
    
    if (counterfactualEnabled) {
      const conversionRateSeries = [];
      const counterfactualRateSeries = [];
      const totalConversions = [];
      const counterfactualConversions = [];
      let index = 0;
      while (index <= position) {
        conversionRateSeries.push(counts[index].totalConversionRate);
        counterfactualRateSeries.push(counts[index].counterfactualRate);
        totalConversions.push(counts[index].totalConversions);
        counterfactualConversions.push(counts[index].counterfactualConversions);
        index += sampleRate;
      }

      series.push({
        animation: {
          duration: 0,
        },
        color: 'black',
        dashStyle: 'solid',
        data: conversionRateSeries,
        lineWidth: 2,
        marker: {
          enabled: false
        },
        name: 'Total Conversion Rate',
        tooltip: {
          split: true,
          valueDecimals: 2,
          valueSuffix: '%',
        },
        type: 'spline',
        yAxis: 1,
      });

      series.push({
        animation: {
          duration: 0,
        },
        color: 'black',
        dashStyle: 'shortdot',
        data: counterfactualRateSeries,
        lineWidth: 2,
        marker: {
          enabled: false
        },
        name: 'Counterfactual Conversion Rate',
        tooltip: {
          split: true,
          valueDecimals: 2,
          valueSuffix: '%',
        },
        type: 'spline',
        yAxis: 1,
      });

      if (showTotals) {
        series.push({
          animation: {
            duration: 0,
          },
          color: 'white',
          data: totalConversions,
          lineWidth: 2,
          marker: {
            enabled: false
          },
          name: 'Total Conversions',
          tooltip: {
            split: true,
          },
          type: 'spline',
          yAxis: 2,
        });

        series.push({
          animation: {
            duration: 0,
          },
          color: 'white',
          dashStyle: 'shortdot',
          data: counterfactualConversions,
          lineWidth: 2,
          marker: {
            enabled: false
          },
          name: 'Counterfactual Conversions',
          tooltip: {
            split: true,
          },
          type: 'spline',
          yAxis: 2,
        });
      }
    }

    return _.cloneDeep(series);
  }

  render() {
    const {
      chartRef,
      counterfactualEnabled,
      distributions,
      policy,
      sampleRate,
      showTotals,
    } = this.props;

    let config = {
      chart: {
        // backgroundColor: 'lightgray',
        height: '36%',
        // style: {
        //   minWidth: '950px',
        // },
        type: counterfactualEnabled ? 'area' : 'line',
        width: 1000,
      },
      credits: {
        enabled: false,
      },
      plotOptions: {
        line: {
          marker: {
            enabled: false,
          },
        },
      },
      tooltip: {
        formatter: function () {
          // The first returned item is the header, subsequent items are the
          // points
          return ['<b>' + this.x * sampleRate + ' visitors</b>'].concat(
            this.points.map(function (point) {
              let pointValue = point.y;
              let suffix = '% of Traffic';
              if (point.series.name.includes('Rate')) {
                suffix = '%';
              }
              if (!point.series.name.includes('Conversions')) {
                pointValue = Number(point.y).toFixed(2) + suffix;
              }
              return point.series.name + ': ' + pointValue;
            })
          );
        },
        split: true,
        valueSuffix: '% of Traffic',
      },
      series: this.getDistributionData(),
      title: {
        text: constants.CHART_LABELS[policy],
      },
      xAxis: {
        labels: {
          formatter: function() {
            return this.value * sampleRate;
          }
        },
        min: 0,
        max: parseInt(distributions[0].length),
        title: {
          text: 'Visitors',
        }
      },
      yAxis: [
        {
          endOnTick: true,
          labels: {
            formatter: function() {
              return this.value + '%';
            },
          },
          title: {
            text: 'Distribution',
          },
        },
      ],
    }

    if (counterfactualEnabled) {
      config.plotOptions = {
        area: {
          lineWidth: 0,
          fillOpacity: 1,
          marker: {
            enabled: false
          },
          stacking: 'percent',
        },
      }

      config.yAxis.push({
        opposite: true,
        labels: {
          formatter: function() {
            return this.value + '%';
          },
        },
        max: 60,
        min: 40,
        title: {
          style: {
            color: 'black',
          },
          text: 'Conversion Rate',
        },
      })
    }

    if (showTotals) {
      config.yAxis.push({
        opposite: true,
        // max: 12000,
        title: {
          style: {
            color: 'white',
          },
          text: 'Conversions',
        },
      });
    }

    return (
      <div className="min-width--200">
        <ReactHighcharts
          config={ config }
          ref={ chartRef }
        />
      </div>
    );
  }
}
