import classNames from 'class-names';
import React, { Component } from 'react';

export default class StatSigCard extends Component {
  formatPValue = () => {
    const { pValue } = this.props;

    return `${ ((1 - pValue) * 100).toFixed(2) }%`;
  }

  render() {
    const {
      color,
      pValue,
      title,
    } = this.props;

    return (
      <div className="flex flex--column flex-align--center soft--sides width--50">
        <h5>
          { title }
        </h5>
        <h3 className={ classNames({
          [color]: pValue <= 0.10
        }) }>
          { this.formatPValue() }
        </h3>
      </div>
    );
  }
}
