import React, { Component } from 'react';

export default class VariationCard extends Component {
  render() {
    const {
      color,
      conversionCount,
      title,
      variationColor,
      visitorCount,
    } = this.props;

    return (
      <div
        className="flex flex--column flex-align--center soft--sides">
        <h5 style={ { 'color': variationColor } }>
          { title }
        </h5>
        <p className="micro muted">
          Conversions
        </p>
        <h3 className={ `${color}` }>
          { conversionCount }
        </h3>
        <p className="micro muted">
          Visitors
        </p>
        <h3 className={ `${color}` }>
          { visitorCount }
        </h3>
        <p className="micro muted">
          Conversion Rate
        </p>
        <h3 className={ `${color}` }>
          { `${visitorCount ? ((conversionCount / visitorCount) * 100).toFixed(2) : '0.00'}%` }
        </h3>
      </div>
    )
  }
}