import _ from 'lodash';
import classNames from 'class-names';
import React, { Component } from 'react';
import './App.css';
import Slider from 'rc-slider';
import Icon from 'react-oui-icons';

import constants from './constants';
import fns from './fns';

import 'rc-slider/assets/index.css';

import DistributionChart from './components/distribution_chart';
import SummaryCard from './components/summary_card';

class App extends Component {
  constructor(props) {
    super(props);

    this.sampleRate = window.uniform.length / 500;
    this.minRegretDistributions = fns.getDistributions(window.minRegret, this.sampleRate);
    this.minTimeDistributions = fns.getDistributions(window.minTime, this.sampleRate);
    this.uniformDistributions = fns.getDistributions(window.uniform, this.sampleRate);
    this.minRegretCounts = fns.getCounts(window.minRegret);
    this.minTimeCounts = fns.getCounts(window.minTime);
    this.uniformCounts = fns.getCounts(window.uniform);
    this.minRegretConfidences = fns.getConfidenceNumbers(window.minRegret);
    this.minTimeConfidences = fns.getConfidenceNumbers(window.minTime);
    this.uniformConfidences = fns.getConfidenceNumbers(window.uniform);
    this.maxVisitors = Math.max(window.minRegret.length, window.minTime.length, window.uniform.length);
    this.minRegretVariations = window.minRegret[0].trafficAllocation.length;
    this.minTimeVariations = window.minTime[0].trafficAllocation.length;
    this.uniformVariations = window.uniform[0].trafficAllocation.length;
    this.chartRefs = {
      minRegretRef: React.createRef(),
      minTimeRef: React.createRef(),
      uniformRef: React.createRef(),
    }
    this.marks = {
      0: {
        style: {
          color: 'grey',
        },
        label: 'Start',
        category: 'ALL',
      },
      [this.maxVisitors]: {
        style: {
          color: 'grey',
        },
        label: 'End',
        category: 'ALL',
      },
    };
    const significance = {
      'minRegret': new Array(this.minRegretVariations - 1).fill(false),
      'minTime': new Array(this.minTimeVariations - 1).fill(false),
      'uniform': new Array(this.uniformVariations - 1).fill(false),
    }

    for (let index = 0; index < this.maxVisitors; index++) {
      const nextTick =  index + (this.sampleRate - index % this.sampleRate) + this.sampleRate * 2;
      if (index) {
        for (let varIndex = 0; varIndex < this.minRegretVariations; varIndex++) {
          if (window.minTime[index].trafficAllocation[varIndex] / window.minTime[index - 1].trafficAllocation[varIndex] < .10) {
            this.marks[nextTick] = {
              style: {
                color: 'gray',
                top: '-35px',
              },
              label: 'Min Time Reallocation',
              category: constants.MIN_TIME,
              key: nextTick,
            }
          }
        }
      }
      for (let varIndex = 0; varIndex < this.uniformVariations - 1; varIndex++) {
        if (significance.minRegret[varIndex] !== window.minRegret[index].stats[(varIndex + 1).toString()].isSignificant) {
          if (window.minRegret[index].stats[(varIndex + 1).toString()].confInterval.lower > 0) {
            this.marks[nextTick] = {
              style: {
                color: 'green',
              },
              label: 'Min Regret Significant',
              category: constants.MIN_REGRET,
              key: nextTick,
            }
          } else {
            this.marks[nextTick] = {
              style: {
                color: 'red',
              },
              label: 'Min Regret Significant',
              category: constants.MIN_REGRET,
              key: nextTick,
            }
          }
          significance.minRegret[varIndex] = !significance.minRegret[varIndex];
        }

        if (significance.minTime[varIndex] !== window.minTime[index].stats[(varIndex + 1).toString()].isSignificant) {
          if (window.minTime[index].stats[(varIndex + 1).toString()].confInterval.lower > 0) {
            this.marks[nextTick] = {
              style: {
                color: 'green',
              },
              label: 'Min Time Significant',
              category: constants.MIN_TIME,
              key: nextTick,
            }
          } else {
            this.marks[nextTick] = {
              style: {
                color: 'red',
              },
              label: 'Min Time Significant',
              category: constants.MIN_TIME,
              key: nextTick,
            }
          }
          significance.minTime[varIndex] = !significance.minTime[varIndex];
        }

        if (significance.uniform[varIndex] !== window.uniform[index].stats[(varIndex + 1).toString()].isSignificant) {
          if (window.uniform[index].stats[(varIndex + 1).toString()].confInterval.lower > 0) {
            this.marks[nextTick] = {
              style: {
                color: 'green',
              },
              label: 'A/B Test Significant',
              category: constants.MANUAL,
              key: nextTick,
            }
          } else {
            this.marks[nextTick] = {
              style: {
                color: 'red',
              },
              label: 'A/B Test Significant',
              category: constants.MANUAL,
              key: nextTick,
            }
          }
          significance.uniform[varIndex] = !significance.uniform[varIndex];
        }
      }
    }
  }

  state = {
    counterfactualEnabled: true,
    currentPosition: 0,
    enabledCharts: [constants.MANUAL, constants.MIN_REGRET],
    hideGraphs: false,
    interval: null,
    isRunning: false,
    marks: {},
    multiplier: 1000,
    showTotals: false,
    showVariationDetail: false,
  };

  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown);
    this.setTimelineMarks();
    // this.setCurrentPosition(this.maxVisitors);
    this.startCounting();
  }

  getNextSignificantPoint = () => {
    const {
      currentPosition,
      marks,
    } = this.state;
    const significantPoints = Object.keys(marks).filter(mark => mark > currentPosition);
    return significantPoints.length ? Number(significantPoints[0]) : currentPosition;
  };

  getPreviousSignificantPoint = () => {
    const {
      currentPosition,
      marks,
    } = this.state;
    const significantPoints = Object.keys(marks).filter(mark => mark < currentPosition);
    return significantPoints.length ? Number(significantPoints[significantPoints.length - 1]) : currentPosition;
  };

  toggleCounterfactual = () => {
    this.setState((previousState) => {
      return {
        counterfactualEnabled: !previousState.counterfactualEnabled,
      };
    });
  };

  toggleSeries = (index) => {
    Object.values(this.chartRefs).forEach((ref) => {
      if (ref.current && ref.current.chart.series[index].visible) {
        ref.current.chart.series[index].hide();
      } else if (ref.current && !ref.current.chart.series[index].visible) {
        ref.current.chart.series[index].show();
      }
    });
  };

  toggleTotals = () => {
    this.setState((previousState) => {
      return {
        showTotals: !previousState.showTotals,
      };
    });
  };

  handleKeyDown = (event) => {
    switch( event.keyCode ) {
      case 32:
        event.preventDefault();
        if (this.state.isRunning) {
          this.stopCounting();
        } else {
          this.startCounting();
        }
        break;
      case 48:
        this.toggleSeries(0);
        break;
      case 49:
        this.toggleSeries(1);
        break;
      case 50:
        this.toggleSeries(2);
        break;
      case 51:
        this.toggleSeries(3);
        break;
      case 65:
        this.toggleChart({ target: { value: constants.MANUAL } });
        break;
      case 67:
        this.toggleCounterfactual();
        break;
      case 69:
        this.stopCounting();
        this.setCurrentPosition(this.maxVisitors);
        break;
      case 71:
        this.toggleCharts();
        break;
      case 73:
        this.toggleChart({ target: { value: constants.MIN_REGRET } });
        break;
      case 76:
        this.toggleChart({ target: { value: constants.MIN_TIME } });
        break;
      case 78:
        this.stopCounting();
        this.setCurrentPosition(this.getNextSignificantPoint());
        break;
      case 80:
        this.stopCounting();
        this.setCurrentPosition(this.getPreviousSignificantPoint());
        break;
      case 82:
        this.reset();
        break;
      case 84:
        this.toggleTotals();
        break;
      default: 
        break;
    }
  };

  isChartEnabled = (chartType) => {
    return this.state.enabledCharts.includes(chartType);
  };

  reset = () => {
    this.stopCounting();
    this.setCurrentPosition(0);
  }

  setCurrentPosition = (position) => {
    this.setState(() => {
      return {
        currentPosition: position
      }
    });
  };

  setTimelineMarks = () => {
    let marks = _.cloneDeep(this.marks)
    const marksToDelete = [];

    Object.values(constants.ALLOCATION_TYPES).forEach((type) => {
      if (!this.isChartEnabled(type)) {
        _.map(marks, (entry) => {
          if (entry.category === type) {
            marksToDelete.push(entry.key);
          }
        });
      }
    });

    marksToDelete.forEach((key) => {
      delete marks[key];
    });

    this.setState(() => {
      return {
        marks,
      };
    });
  };

  startCounting = () => {
    if (this.state.currentPosition >= this.maxVisitors) {
      this.setState(() => {
        return {
          currentPosition: 0,
        };
      }, this.startInterval);
    } else {
      this.startInterval();
    }
    this.setState(() => {
      return {
        isRunning: true,
      };
    });
  };

  startInterval = () => {
    this.setState(() => {
      return {
        interval: setInterval(() => {
          if (this.state.currentPosition >= this.maxVisitors) {
            this.stopCounting();
            this.startCounting();
            return;
          }
          this.setState((_previousState) => {
            return {
              currentPosition: _previousState.currentPosition + _previousState.multiplier >= this.maxVisitors ? this.maxVisitors : _previousState.currentPosition + _previousState.multiplier,
            };
          });
        }, 100),
      }
    });
  };

  stopCounting = () => {
    clearInterval(this.state.interval);
    this.setState(() => {
      return {
        interval: null,
        isRunning: false,
      };
    });
  };

  toggleChart = (event) => {
    const chartType = event.target.value;

    if (this.state.enabledCharts.includes(chartType)) {
      const charts = _.cloneDeep(this.state.enabledCharts);
      this.setState(() => {
        return {
          enabledCharts: charts.filter(type => type !== chartType),
        };
      }, this.setTimelineMarks);
    } else {
      this.setState((previousState) => {
        const charts = _.cloneDeep(previousState.enabledCharts)
        charts.push(chartType);
        return {
          enabledCharts: charts,
        };
      }, this.setTimelineMarks);
    }
  };

  toggleCharts = (event) => {
    this.setState((previousState) => {
      return {
        hideGraphs: !previousState.hideGraphs,
      }
    })
  };

  render() {
    return (
      <div className="flex flex-align--center width--100 flex--space">
        <div className="soft--sides width--1200">
          <div className="push--ends flex flex--row flex-align--center display--none">
            <div className={ classNames({
              'push-double--right': true,
              'flex': true,
              'flex--column': true
            }) }>
              <div className="flex--row micro">
                <input
                  checked={ this.isChartEnabled(constants.MANUAL) }
                  id={ constants.MANUAL }
                  onChange={ this.toggleChart }
                  type="checkbox"
                  value={ constants.MANUAL }
                />
                <label htmlFor={ constants.MANUAL }>
                  A/B
                </label>
              </div>
              <div className="flex--row micro">
                <input
                  checked={ this.isChartEnabled(constants.MIN_REGRET) }
                  id={ constants.MIN_REGRET }
                  onChange={ this.toggleChart }
                  type="checkbox"
                  value={ constants.MIN_REGRET }
                />
                <label htmlFor={ constants.MIN_REGRET }>
                  Min Regret
                </label>
              </div>
              <div className="flex--row micro">
                <input
                  checked={ this.isChartEnabled(constants.MIN_TIME) }
                  id={ constants.MIN_TIME }
                  onChange={ this.toggleChart }
                  type="checkbox"
                  value={ constants.MIN_TIME }
                />
                <label htmlFor={ constants.MIN_TIME }>
                  Min Time
                </label>
              </div>
              <div className="flex--row micro">
                <input
                  checked={ this.state.hideGraphs }
                  id="charts"
                  onChange={ this.toggleCharts }
                  type="checkbox"
                />
                <label htmlFor="charts">
                  Hide Graphs
                </label>
              </div>
            </div>
            <div className="push-double--right">
              { !this.state.interval && (
                <button
                  className="no-padding"
                  onClick={ this.startCounting }>
                  <div className="flex square--50 flex-align--center flex-justify--center">
                    { this.state.currentPosition !== this.maxVisitors && (
                      <Icon
                        name="play"
                        size="large"
                      />
                    ) }
                    { this.state.currentPosition === this.maxVisitors && (
                      <Icon
                        name="refresh"
                        size="large"
                      />
                    ) }
                  </div>
                </button>
              ) }
              { this.state.interval && (
                <button
                  className="no-padding"
                  onClick={ this.stopCounting }>
                  <div className="flex square--50 flex-align--center flex-justify--center">
                    <Icon
                      name="pause"
                      size="large"
                    />
                  </div>
                </button>
              ) }
            </div>
            <div className="flex flex--1 flex-align--center push-double--right">
              <Slider
                dots={ false }
                min={ 0 }
                marks={ this.state.marks }
                max={ this.maxVisitors }
                onChange={ this.setCurrentPosition }
                value={ this.state.currentPosition }
              />
            </div>
          </div>
          <div className={ classNames({
            'flex': this.state.hideGraphs,
            'flex--row': this.state.hideGraphs,
            'flex-justify--center': this.state.hideGraphs,
            'flex--space': this.state.hideGraphs,
          }) }>
            { this.isChartEnabled(constants.MANUAL) && (
              <div className="flex flex--row border push--ends overflow--visible">
                { !this.state.hideGraphs && (
                  <DistributionChart
                    chartRef={ this.chartRefs.uniformRef }
                    counterfactualEnabled={ this.state.counterfactualEnabled }
                    counts={ this.uniformCounts }
                    distributions={ this.uniformDistributions }
                    policy={ constants.MANUAL }
                    position={ this.state.currentPosition }
                    sampleRate={ this.sampleRate }
                    showTotals={ this.state.showTotals }
                    variations={ this.uniformVariations }
                  />
                ) }
                <SummaryCard
                  confidences={ this.uniformConfidences }
                  counts={ this.uniformCounts }
                  hideGraphs={ this.state.hideGraphs }
                  policy={ constants.MANUAL }
                  position={ this.state.currentPosition }
                  showVariationDetail={ this.state.showVariationDetail }
                  variations={ this.uniformVariations }
                />
              </div>
            ) }
            { this.isChartEnabled(constants.MIN_REGRET) && (
              <div className="flex flex--row border push--ends overflow--visible">
                { !this.state.hideGraphs && (
                  <DistributionChart
                    chartRef={ this.chartRefs.minRegretRef }
                    counterfactualEnabled={ this.state.counterfactualEnabled }
                    counts={ this.minRegretCounts }
                    distributions={ this.minRegretDistributions }
                    policy={ constants.MIN_REGRET }
                    position={ this.state.currentPosition }
                    sampleRate={ this.sampleRate }
                    showTotals={ this.state.showTotals }
                    variations={ this.minRegretVariations }
                  />
                ) }
                <SummaryCard
                  confidences={ this.minRegretConfidences }
                  counts={ this.minRegretCounts }
                  hideGraphs={ this.state.hideGraphs }
                  policy={ constants.MIN_REGRET }
                  position={ this.state.currentPosition }
                  showVariationDetail={ this.state.showVariationDetail }
                  variations={ this.minRegretVariations }
                />
              </div>
            ) }
            { this.isChartEnabled(constants.MIN_TIME) && (
              <div className="flex flex--row border push--ends overflow--visible">
                { !this.state.hideGraphs && (
                  <DistributionChart
                    chartRef={ this.chartRefs.minTimeRef }
                    counterfactualEnabled={ this.state.counterfactualEnabled }
                    counts={ this.minTimeCounts }
                    distributions={ this.minTimeDistributions }
                    policy={ constants.MIN_TIME }
                    position={ this.state.currentPosition }
                    sampleRate={ this.sampleRate }
                    showTotals={ this.state.showTotals }
                    variations={ this.minTimeVariations }
                  />
                ) }
                <SummaryCard
                  confidences={ this.minTimeConfidences }
                  counts={ this.minTimeCounts }
                  hideGraphs={ this.state.hideGraphs }
                  policy={ constants.MIN_TIME }
                  position={ this.state.currentPosition }
                  showVariationDetail={ this.state.showVariationDetail }
                  variations={ this.minTimeVariations }
                />
              </div>
            ) }
          </div>
        </div>
      </div>
    );
  }
}

export default App;
