import classNames from 'class-names';
import React, { Component } from 'react';

import constants from '../../constants';

import StatSigCard from '../stat_sig_card';
import VariationCard from '../variation_card';

export default class SummaryCard extends Component {
  getVisitorCount = (variation) => {
    const {
      counts,
      position,
    } = this.props;

    return counts[position].visitors[variation];
  }

  getConversionCount = (variation) => {
    const {
      counts,
      position,
    } = this.props;

    return counts[position].conversions[variation];
  }

  getPValue = (variation) => {
    const {
      confidences,
      position,
    } = this.props;

    return confidences[position].pValues[variation];
  }

  isBetterThanBaseline = (variation) => {
    const baselineConversionRate = this.getConversionCount(0) / this.getVisitorCount(0);
    const variationConversionRate = this.getConversionCount(variation) / this.getVisitorCount(variation);
    return Number.isNaN(baselineConversionRate) || Number.isNaN(variationConversionRate) ? false : variationConversionRate > baselineConversionRate;
  };

  renderVariationCards = () => {
    const {
      showVariationDetail,
      variations,
    } = this.props;

    let conversionTotal = 0;
    let visitorTotal = 0;

    return Array(variations + 1).fill(1).map((value, index) => {
      if (index < variations) {
        conversionTotal += this.getConversionCount(index);
        visitorTotal += this.getVisitorCount(index);
      }
      if (!index && showVariationDetail) {
        return (
          <VariationCard
            color="black"
            conversionCount={ this.getConversionCount(0) }
            key={ index }
            title="Original"
            variationColor={ constants.COLORS[index] }
            visitorCount={ this.getVisitorCount(0) }
          />
        );
      } else if (index === variations) {
        return (
          <VariationCard
            color="black"
            conversionCount={ conversionTotal }
            key={ index }
            title="Total"
            variationColor='black'
            visitorCount={ visitorTotal }
          />
        );
      } else if (showVariationDetail) {
        return (
          <VariationCard 
            color={ this.getPValue(index - 1) <= 0.10 ? this.isBetterThanBaseline(index) ? 'green' : 'red' : 'black' }
            conversionCount={ this.getConversionCount(index) }
            key={ index }
            title={ `Variation ${index}` }
            variationColor={ constants.COLORS[index] }
            visitorCount={ this.getVisitorCount(index) }
          />
        );
      } else {
        return null;
      }
    });
  };

  renderStatSigCards = () => {
    const { variations } = this.props;

    return Array(variations - 1).fill(1).map((value, index) => {
      return (
        <StatSigCard
          color={ this.isBetterThanBaseline(index + 1) ? 'green' : 'red' }
          key={ index }
          pValue={ this.getPValue(index) }
          title={ `Variation ${index + 1}` }
        />
      )
    });
  };

  render() {
    const {
      hideGraphs,
      policy,
      showVariationDetail,
      variations,
    } = this.props;
    return (
      <div
        className={ classNames({
          "border--left": !hideGraphs,
          "flex": true,
          "flex-align--center": true,
          "flex--column": true,
          "soft": hideGraphs,
          "flex--space": true,
          "flex--1": true,
        }) }
        style={ {
          minWidth: `${125 * (showVariationDetail ? variations : 1)}px`,
        } }
      >
        { hideGraphs && (
          <h1>{ constants.CHART_LABELS[policy] }</h1>
        ) }
        <div className="flex flex--row flex--space width--100">
          { this.renderVariationCards() }
        </div>
        { showVariationDetail && (
          <React.Fragment>
            <h4 className="flex">
              Statistical Significance:
            </h4>
            <div className="flex flex--row flex--space width--100">
              { this.renderStatSigCards() }
            </div>
          </React.Fragment>
        ) }
      </div>
    )
  }
}